/* src/App.css */

/* Body and general styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  overflow-x: hidden; /* Prevent horizontal scroll */
  background-color: #111; /* Dark background */
  color: #fff; /* Light text color */
}

/* Navbar styles */
nav {
  background: #333; /* Darker navbar background */
  position: sticky; /* Keep navbar at the top */
  top: 0;
  width: 100%; /* Ensure navbar spans the full width */
  z-index: 1000; /* Ensure it stays on top */
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: space-around; /* Space out the navbar items */
  margin: 0;
  padding: 1rem; /* Adjust padding for desired height */
}

nav ul li {
  display: inline;
}

nav ul li a {
  text-decoration: none;
  color: #fff; /* Set link color to white */
  font-size: 1.2rem;
  padding: 0.5rem; /* Add padding for clickable area */
  cursor: pointer;
}

.content {
  padding-top: 3rem; /* Adjust based on the height of your navbar */
}

section {
  padding: 3rem 1rem;
  border-bottom: 1px solid #ccc;
  margin: 0; /* Remove default margin */
}

section:last-of-type {
  border-bottom: none;
}

.section-divider {
  margin: 2rem 0; /* Adjust margin as needed */
  border: none;
  border-top: 1px solid #ccc; /* Style of the divider line */
}

.section-with-margin {
  margin-left: 1rem; /* Adjust left margin using rem or % */
}
