/* src/components/Skills.css */
.skills-list {
    list-style: none;
    padding: 0;
  }
  
  .skills-list li {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  

  .skills-container {
  padding: 20px;
  background-color: #333; /* Dark background */
  color: #fff; /* Light text color */
  border-radius: 8px; /* Rounded corners */
}

.skill {
  margin-bottom: 10px;
}

.skill-name {
  font-weight: bold;
}

.skill-bar {
  height: 8px;
  background-color: #4aaff7; /* Accent color */
  border-radius: 4px; /* Rounded corners */
}


